<template>
  <div class="panel panel-default container">
    <!-- <div class="card">
      <div class="card-body">
        <h1>:</h1>
      </div>

      <div>
        :
        <input type="text" v-model="blogData.title" class="form-control" />
        :
        <div class="form-group">
          <textarea v-model="blogData.body" class="form-control" rows="5" id="comment">{{blogData.title}}</textarea>
        </div>
        <select name="tagCloud" id="tag" v-model="blogData.tagCloud">
          <option value="vue">Vue</option>
          <option value="css3">Css3</option>
        </select>
        <button type="submit" class="btn btn-primary" v-on:click="sendBlog">Submit</button>
      </div>
    </div>-->
    <p class="blogTitle"></p>
    <input type="text" v-model="blogData.title" class="title form-control" />
    <p class="blogIntro"></p>
    <input type="text" v-model="blogData.Intro" class="title form-control" />
    <div id="main">
      <mavon-editor class="mavon" ref="editor" @change="UpdataDoc" v-model="blogData.markdown" />
    </div>
    <button
      type="btn"
      :toolbars="toolbars"
      class="btn btn-primary"
      v-on:click="saveDoc(blogData.markdown,blogData.html),routerTo()"
    ></button>
  </div>
</template>

<script>
export default {
  name: "sendBlog",
  data() {
    return {
      blogData: {
        title: "",
        Intro: "",
        tagCloud: [],
        author: "",
        html: "",
        markdown: ""
      },
      toolbars: {
        bold: true, // 
        italic: true, // 
        header: true, // 
        underline: true, // 
        mark: true, // 
        superscript: true, // 
        quote: true, // 
        ol: true, // 
        link: true, // 
        imagelink: true, // 
        help: true, // 
        code: true, // code
        subfield: true, // 
        fullscreen: true, // 
        readmodel: true, // 
        /* 1.3.5 */
        undo: true, // 
        trash: true, // 
        // save: true, // eventssave
        /* 1.4.2 */
        navigation: true // 
      }
    };
  },
  methods: {
    // $refs.editor.d_value,$refs.editor.d_render
    //
    UpdataDoc(markdown, html) {
      this.blogData.markdown = markdown;
      this.blogData.html = html;
    },
    //save
    saveDoc(markdown, html) {
      let data = {
        markdown: markdown,
        html: html,
        title: this.blogData.title,
        Intro: this.blogData.Intro
      };
      //ID
      //
      let url;
      if (this.$route.params.userId) {
        console.log("put");
        console.log(data);
        url = "/Editblog/" + this.$route.params.userId;
        this.axios.post(url, data).then(data => {
          console.log(data);
        });
      } else {
        console.log("post");
        url = "/sendBlog";
        this.axios.post(url, data).then(data => {
          console.log(data);
        });
      }
    },
    //ID
    getBlog(id) {
      this.axios
        .get(`/findBlog/` + id)
        .then(data => {
          this.blogData = data.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    routerTo() {
      this.$router.push({
        name: "deleteBlog"
      });
    }
  },
  mounted: function() {
    //ID
    if (this.$route.params.userId) {
      this.getBlog(this.$route.params.userId);
    }
  }
};
</script>

<style scoped>
h1 {
  margin: 10px;
}

.blogTitle {
  text-align: left;
}

.blogIntro {
  text-align: left;
}

.title {
  margin-bottom: 10px;
}
.mavon {
  height: 70vh;
}

.container {
  display: inline-block;
  min-height: 90vh;
}

.btn {
  margin: 10px;
  float: right;
}

.card {
  border: 1px solid red;
}
#tag {
  float: left;
}
</style>
