import Vue from 'vue';
import App from './App.vue';
import VuerRouter from "vue-router";
import './registerServiceWorker';
import animated from 'animate.css'; // npm install animate.css --save
import axios from 'axios';
import qs from 'qs'
import mavonEditor from 'mavon-editor' //
import 'mavon-editor/dist/css/index.css'
import Mybody from './components/body/Mybody.vue'; // router
import {
  routes
} from './router/router' //
import hljs from 'highlight.js/lib/highlight'; //
import javascript from 'highlight.js/lib/languages/javascript'; //js
import swal from 'sweetalert';


//mavon markdown
Vue.use(mavonEditor)

//
hljs.registerLanguage('javascript', javascript);

//axios
Vue.prototype.axios = axios;
Vue.prototype.HOST = '/api'
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'; // 
axios.defaults.baseURL = process.env.VUE_APP_URL || '/api'
// axios.defaults.baseURL = 'http://localhost:3333/api';

//axios
axios.interceptors.request.use(function (config) {
  config.headers['Content-Type'] = 'application/x-www-form-urlencoded'
  if (config.method === 'post') { // post
    config.data = qs.stringify({
      ...config.data //axiospostjson,qs.
    })
  }
  return config;
}, function (error) {
  loadinginstace.close()
  return Promise.reject(error);
})

//axios
axios.interceptors.response.use(function (response) {
  return response;
}, function (error) {
  return Promise.reject(error);
});



Vue.use(VuerRouter)
Vue.use(animated)
Vue.config.productionTip = false
Vue.component('Mybody', Mybody)
//
Vue.filter("Search", function (value) {
  return
})


const router = new VuerRouter({
  routes,
  mode: "history"
})


new Vue({
  router,
  render: h => h(App),
  components: {
    Mybody
  }

}).$mount('#app')